import type {
  ChainEnum,
  VerifyResponse,
  WalletAddressType,
  WalletProviderEnum,
} from '@dynamic-labs/sdk-api-core';

import { getCore } from '../../../client/core/getCore';
import type { DynamicClient } from '../../../client/types';
import { isErrorWithCode } from '../../../utils/isErrorWithCode';
import { createApiClient } from '../../apiClient';
import { consumeCaptchaToken } from '../../captcha/consumeCaptchaToken';
import type { Chain } from '../../chain';
import { normalizeWalletNameWithChain } from '../utils/normalizeWalletNameWithChain';
import type { WalletAddressWithType } from '../walletAccount/walletAccount.types';
import { WalletAlreadyLinkedToAnotherUserError } from './../../../errors/WalletAlreadyLinkedToAnotherUserError';

type VerifyMessageSignatureOwnershipParams = {
  addressesWithTypes?: WalletAddressWithType[];
  chain: Chain;
  isTransfer?: boolean;
  messageToSign: string;
  signature: string;
  walletAddress: string;
  walletDisplayName: string;
  walletProviderType: WalletProviderEnum;
};

/**
 * Verifies a signed message by calling either the verifyLink, verifyTransfer or verify API function,
 * depending on whether the user is signing in, linking or transferring a wallet.
 *
 * Does NOT call updateAuthFromVerifyResponse, it should be called from the return of this function.
 */
export const verifyMessageSignatureOwnership = async (
  {
    messageToSign,
    walletAddress,
    addressesWithTypes,
    signature,
    chain,
    isTransfer = false,
    walletDisplayName,
    walletProviderType,
  }: VerifyMessageSignatureOwnershipParams,
  client: DynamicClient
): Promise<VerifyResponse> => {
  const core = getCore(client);
  const apiClient = createApiClient({}, client);

  const walletName = normalizeWalletNameWithChain({
    chain,
    displayName: walletDisplayName,
  });

  let verifyApiFunction;

  if (!client.user) {
    verifyApiFunction = apiClient.verify.bind(apiClient);
  } else if (isTransfer) {
    verifyApiFunction = apiClient.verifyTransfer.bind(apiClient);
  } else {
    verifyApiFunction = apiClient.verifyLink.bind(apiClient);
  }

  try {
    return await verifyApiFunction({
      environmentId: core.environmentId,
      verifyRequest: {
        // mapping to handle typecasting for address type
        additionalWalletAddresses: addressesWithTypes?.map((address) => ({
          address: address.address,
          publicKey: address.publicKey,
          type: address.type as WalletAddressType,
        })),
        captchaToken: consumeCaptchaToken(client),
        // eslint-disable-next-line custom-rules/ban-chain-enum
        chain: chain as ChainEnum,
        messageToSign,
        publicWalletAddress: walletAddress,
        signedMessage: signature,
        walletName,
        walletProvider: walletProviderType,
      },
    });
  } catch (error) {
    if (
      isErrorWithCode(error, 'reassign_wallet_confirm') ||
      isErrorWithCode(error, 'merge_accounts_confirmation')
    ) {
      throw new WalletAlreadyLinkedToAnotherUserError({
        cause: error,
      });
    }

    throw error;
  }
};
